import { isPlatformBrowser } from '@angular/common';
import { ChangeDetectorRef, Component, Inject, OnInit, PLATFORM_ID, TemplateRef, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Gallery, GalleryItem } from 'ng-gallery';
import { Lightbox } from 'ng-gallery/lightbox';
import { Subject, takeUntil } from 'rxjs';
import { AdminService } from 'src/app/admin/admin.service';
import { environment } from 'src/environments/environment';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-editar-parametro',
  templateUrl: './editar-parametro.component.html',
  styleUrls: ['../../parameters.component.scss']
})
export class EditarParametroComponent implements OnInit {

  @ViewChild("modalEditarParametro", { static: false }) ModalEditarParametro: TemplateRef<any>;
  private _unsubscribeAll: Subject<any> = new Subject<any>();

  public closeResult: string;
  public modalOpen: boolean = false;

  user: any = {};

  Toast: any;

  loading: boolean = true;

  acepting: boolean = true;
  limit: any = environment.pagination_limit;

  descriptionControl: FormControl = new FormControl({value: '', disabled: true}, [Validators.required]);

  valueControl: FormControl = new FormControl('', [Validators.required]);

  parameter: any = {};

  constructor(
    @Inject(PLATFORM_ID) private platformId: Object,
        private router: Router, private modalService: NgbModal,
        private _formBuilder: FormBuilder,
        private _changeDetectorRef: ChangeDetectorRef,
        private _adminService: AdminService, public gallery: Gallery, public lightbox: Lightbox
  ) { 
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    });  
  }

  ngOnInit() {
    this._adminService.parameterSelected$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((parameter: any) => {
        this.loading = true;
        this.parameter = parameter;
        this.loading = false;
        if(parameter?.nombre){
          this.descriptionControl.setValue(parameter.nombre);
          this.valueControl.setValue(parameter.valor);
        }
        // console.log(this.user);

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
  }

  ngOnDestroy() {
    if(this.modalOpen){
      this.modalService.dismissAll();
    }
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }

  actualizarParametro(){

  }

  openModal() {
    this.acepting = true;
    this.modalOpen = true;
    if (isPlatformBrowser(this.platformId)) {
      // For SSR
      this.modalService
        .open(this.ModalEditarParametro, {
          size: "lg",
          ariaLabelledBy: "modal-basic-title",
          centered: true,
          windowClass: "ModalEditarParametro",
          backdrop: "static",
          keyboard: false,
        })
        .result.then(
          (result) => {
            `Result ${result}`;
          },
          (reason) => {
            this.closeResult = `Dismissed`;
          }
        );
    }
  }
}
