import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { Subject, takeUntil } from 'rxjs';
import { environment } from 'src/environments/environment';
import { AdminService } from '../admin.service';
import { EditarParametroComponent } from './modals/editar-parametro/editar-parametro.component';

@Component({
  selector: 'app-parameters',
  templateUrl: './parameters.component.html',
  styleUrls: ['./parameters.component.scss']
})
export class ParametersComponent implements OnInit {

  @ViewChild("editarParameter") EditarModal: EditarParametroComponent;

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  parametersCount: number = 0;
  parametersList: any = [];
  page: any = 1;
  limit: any = environment.pagination_limit;
  Toast:any;

  parameterSelected:any = {};

  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _adminService: AdminService
  ) { }

  ngOnInit() {
    //Add 'implements OnInit' to the class.
    this._adminService.parametersList$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((parameters: any) => {
        // Update the selected agente
        this.parametersList = parameters.parametros;
        this.parametersCount = parameters.totalDocuments;

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
  }

  /**
  * On destroy
  */
  ngOnDestroy(): void
  {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next(null);
      this._unsubscribeAll.complete();
  }

  abrirModalEditarParametro(parametro:any){
    this._adminService.setParameterSelected(parametro);
    this.EditarModal.openModal();
  }

}
